header {
  position: fixed;
  top: -37px;
  left: 0;
  width: 100%;
  z-index: 11;
  letter-spacing: .15em;
  padding: 0 64px;
  transition: .4s;
  &:after {
    content: '';
    position: absolute;
    top: 88px;
    left: 32px;
    right: 32px;
    height: 70px;
    background: #fff;
    pointer-events: none;
    opacity: 0;
    transition: .4s;
    z-index: -2;
  }
  &.scroll {
    top: -88px;
    &:after {
      opacity: 1;
      transition: top .4s .3s, height .6s;
    }
    &.open_drop {
      &:after {
        height: 175px;
      }
    }
  }
  .page {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      bottom: -132px;
      left: 0;
      right: 0;
      height: 1px;
      background: rgba(#fff,.2);
    }
  }
  .responsive_wrap {
    .contetn_resp {
      flex-grow: 1;
      display: flex;
    }
  }
  .bot_resp {
    display: inline-flex;
    flex-grow: 1;
    justify-content: flex-end;
  }
  .header_contact {
    display: none;
  }
  a {
    padding: 10px;
    color: #fff;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    position: relative;
    svg:not(.doc_svg):not(.logo_olir_svg) {
      content: '';
      position: absolute;
      top: 6px;
      left: 5px;
      width: 12px;
      height: 18px;
      z-index: -1;
      transition: .45s cubic-bezier(.165,.84,.44,1), fill 1.5s;
      transform: translate(-5px, -10px);
      opacity: 0;
      transform-origin: 0 0;
    }
    svg {
      fill: $y;
    }
    &:not(.active):not(.logo_olir_svg):hover {
      svg {
        transform: translate(0);
        opacity: 1;
      }
    }
    &.active {
      color: $y;
    }
  }

  .pdf {
    margin: auto 42px auto auto;
    padding-left: 28px;
    top: 100px;
    z-index: 5;
    svg {
      position: absolute;
      top: 50%;
      left: 0;
      margin-top: -11px;
      fill: $y;
    }
    span {
      border-bottom: 1px solid transparent;
      padding: 0 0 2px;
      transition: .2s;
    }
    &:hover span {
      border-color: $y;
    }
  }


}

#menu_btn {
  width: 26px;
  height: 19px;
  position: relative;
  top: 115px;
  display: none;
  span {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    height: 3px;
    width: 100%;
    background: #fff;
    transition: .3s;
    &.middle {
      top: 8px;
    }
    &.bottom {
      width: 18px;
      top: 16px;
      left: 8px;
    }
  }
}
#header_navigation {
  position: absolute;
  width: 100%;
  text-align: center;
  top: 109px;
  left: 0;
  z-index: 2;
  .item {
    margin: 0 15px;
    position: relative;
    display: inline;
    &.active {
      color: $y;
    }
    a {
      margin: 0;
    }
    &.open .drop_menu {
      pointer-events: auto;
      a {
        opacity: 1;
        &:nth-child(1) {
          transition-delay: 0s;
        }
        &:nth-child(2) {
          transition-delay: 0.1s;
        }
        &:nth-child(3) {
          transition-delay: 0.2s;
        }
      }
    }
  }
  .drop_menu {
    position: absolute;
    top: 26px;
    left: 0;
    text-align: left;
    line-height: 30px;
    pointer-events: none;
    a {
      white-space: nowrap;
      display: block;
      padding: 0 10px;
      margin: 0;
      opacity: 0;
      transition: 0.3s ease;
      &:nth-child(1) {
        transition-delay: 0.2s;
      }
      &:nth-child(2) {
        transition-delay: 0.1s;
      }
      &:nth-child(3) {
        transition-delay: 0s;
      }
      &:hover {
        color: $y;
      }
    }
  }
}
#logo {
  position: relative;
  top: 65px+37px;
  z-index: 3;
  padding: 0;
  height: 38px;
  width: 102px;
  opacity: 0;
  svg {
    position: absolute;
    top: 0;
    left: 0;
    transition: .3s;
    height: 38px;
    &.logo_olir_svg1 {
      width: 38px;
    }
    &.logo_olir_svg2 {
      width: 100%;
    }
    .text {
      fill: #fff;
    }
  }
  #drib_logo {
    transition: 1s;
    transform-origin: 50%;
    path {
      transform-origin: 50%;
      transition: .4s;
    }
  }
  &:hover {
    #drib_logo {
      transform: rotate(360deg);
      path {
        transform: scale(.8);
      }
    }
  }
}
#lang {
  position: relative;
  top: 109px;
  z-index: 2;
  a {
    padding: 10px 8px;
    svg:not(.doc_svg) {
      left: 2px;
    }
    &.active {
      color: $y;
    }
  }
}
.dark header {
  a {
    color: #000;
  }
  #logo_preload,
  #logo {
    .text {
      fill: #0a5993 !important;
    }
  }
  .page:after {
    content: none;
  }
  #menu_btn span {
    background: #000;
  }
}




