.container {
  width: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 32px;
  transition: 3s;
  background: $y;
}

main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
}
footer {
  margin: auto auto 0;
  position: relative;
  &.hideFooter {
    opacity: 0;
    display: none;
  }
}


#slides_main {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
#slides_main_wrapp {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.video_index {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.svg-mask-common {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 4;
  /*pointer-events: none;
  opacity: 0;*/
}
#svg_mask_resp {
  display: none;
  width: 600px;
  height: 1000px;
  position: absolute;
  top: 0;
  left: 0;
  .mask {
    transform-origin: 0 0;
    transform: scale(10);
    transition: transform .7s ease-out;
  }
}
.go_drib {
  #svg_mask_resp {
    .mask {
      transform: scale(1) translate(0);
    }
  }
}
.drib_svg_part {
  transform-origin: 50%;
}
.svg-mask-common mask rect,
.svg-mask-common mask .circle {
  transition: 2s;
}

.svg-mask-common mask rect,
.svg-mask-common mask .circle_gray {
  fill: rgba(255, 255, 255, 1);
}
.svg-mask-common > rect,
.svg-mask-common > circle {
  fill: rgba(246, 246, 246, 1);
}

#drib_border_1_r,
#drib_border_2_r,
#drib_border_1,
#drib_border_2 {
  transform-origin: 50%;
  transform: scale(.6);
  opacity: 0;
  transition: .3s;
  transition-delay: 0s;
}

#drib_border_1_r,
#drib_border_1 {
  transform: scale(.4);
  transition-delay: 0s;
}

#drib_1,
#drib_2,
#drib_3,
#drib_4 {
  opacity: 0;
  transform: translate(-20px, -30px);
  transition: .6s;
  transition-delay: 0s;
}


.drib_mask {
  transform: scale(18);
  transform-origin: 20% 58%;
  transition: transform 1.4s ease-out;
}

#play_mini,
#play {
  cursor: pointer;
  opacity: 0;
  transform: scale(1.4);
  transition: .7s;
  transition-delay: 0s;
  transform-origin: 24% 58%;
}

.slide_main {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  &.visible {
    z-index: 2;
    h2 {
      transition: .3s 1.8s;
      transform: translateY(0);
      opacity: 1;
      svg {
        transition: .5s 2s;
        transform: translate(0, 0);
        opacity: 1;
      }
    }
    .skip {
      transition: .3s 1.8s;
      transform: translateY(0);
      opacity: 1;
    }
  }
  .text {
    position: relative;
    margin: 0 0 58px 200px;
    z-index: 3;
    transition: .6s;
  }
  h2 {
    letter-spacing: .1em;
    font-size: 48px;
    line-height: 60px;
    font-weight: 700;
    text-transform: uppercase;
    position: relative;
    margin-bottom: 41px;
    transition: .3s;
    transform: translateY(-20px);
    opacity: 0;
    svg {
      position: absolute;
      top: -63px;
      left: -39px;
      z-index: -1;
      transition: .3s;
      transform: translate(-15px, -30px);
      opacity: 0;
    }
  }
  .skip {
    color: #fff;
    position: relative;
    font-size: 12px;
    font-weight: 300;
    padding: 20px 20px 20px 97px;
    letter-spacing: .1em;
    transition: .3s;
    transform: translateY(20px);
    opacity: 0;
    span {
      transition: .2s;
      display: inline-block;
    }
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      margin-top: -1px;
      height: 1px;
      width: 64px;
      background: #fff;
      transition: .2s;
    }
  }
}

@media all and (min-width: 1025px) {
  .slide_main .skip:hover {
    span {
      transform: translateX(15px);
    }
    &:after {
      width: 74px;
    }
  }
}

.content_slide_main {
  overflow: hidden;
  transition: .4s .1s ease;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  color: #fff;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url(../img/bg_video.png) repeat;
    z-index: 2;
  }
}


#nav_slides_main {
  position: absolute;
  bottom: 50px;
  left: 42px;
  text-align: center;
  z-index: 3;
}
.item_nav_slides_main {
  color: #fff;
  display: block;
  font-size: 16px;
  font-weight: 700;
  position: relative;
  margin: 17px 0;
  &:before {
    content: '';
    position: absolute;
    top: 10px;
    left: -42px;
    width: 0;
    height: 2px;
    background: $y;
    transition: opacity .6s, width .3s, background 1.5s;
    opacity: 0;
  }
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -5px 0 0 -5px;
    width: 7px;
    height: 7px;
    border: 1px solid #f8f8f8;
    border-radius: 100%;
    opacity: 1;
    transform: scale(1);
    transition: transform .55s .2s cubic-bezier(.165,.84,.44,1),opacity .45s .2s cubic-bezier(.165,.84,.44,1);
  }
  span {
    opacity: 0;
    transition: transform .7s cubic-bezier(.23,1,.32,1),opacity .45s cubic-bezier(.165,.84,.44,1);
    display: inline-block;
    &.f {
      transform: translateY(-10px);
    }
    &.l {
      transform: translateY(10px);
    }
  }
  &.active {
    span {
      opacity: 1;
      transition: transform .7s .2s cubic-bezier(.23,1,.32,1),opacity .45s .2s cubic-bezier(.165,.84,.44,1);
      &.f,
      &.l {
        transform: translateY(0);
      }
    }
    &:before {
      width: 41px;
      opacity: 1;
    }
    &:after {
      opacity: 0;
      transform: scale(1.3);
      transition: transform .55s cubic-bezier(.165,.84,.44,1),opacity .45s cubic-bezier(.165,.84,.44,1);
    }
  }
}

.blockScroll {
  .item_nav_slides_main:before {
    width: 0;
    opacity: 1;
  }
}

#arrow,
#next_slide_main {
  cursor: pointer;
  width: 43px;
  height: 43px;
  display: block;
  border: 1px solid #fff;
  border-radius: 100%;
  background: url(../img/next_slide.svg) center no-repeat;
  margin: 33px 0 0;
}

#arrow {
  position: absolute;
  bottom: 73px;
  left: 50%;
  border-color: #000;
  margin: 0 0 0 -22px;
  background: url(../img/arrow.svg) center no-repeat;
  opacity: 0;
  transition: .6s;
  transform: translateY(-35px);
  z-index: 4;
  &:after {
    content: '';
    position: absolute;
    left: 50%;
    margin-left: -1px;
    bottom: 0;
    width: 1px;
    height: 0;
    background: #000;
    transition: .5s;
    transition-delay: .6s;
  }
  &.go_an {
    transform: translateY(0);
    opacity: 1;
    &:after {
      bottom: -68px;
      height: 46px;
    }
  }
}



#content_block {
  background: #fff;
  flex-grow: 1;
  top: 0;
  left: 0;
  z-index: 10;
  /*height: 100%;*/
}
.index_page {
