}


.go_drib {
  .drib_mask {
    transform: scale(1);
  }
  #play_mini,
  #play {
    opacity: 1;
    transform: scale(1);
    transition-delay: 1.3s;
  }

  #drib_border_1_r,
  #drib_border_2_r {
    opacity: 1;
    transform: scale(1);
    transition-delay: .9s !important;
    transition: .2s;
  }
  #drib_border_1,
  #drib_border_2 {
    opacity: 1;
    transform: scale(1);
    transform-origin: 24% 64%;
    transition-delay: 1s !important;
    transition: .9s;
  }
  #drib_border_1_r {
    transition: .2s;
    transition-delay: .8s !important;
  }
  #drib_border_1 {
    transition-delay: 1.1s !important;
    transition: .9s;
  }
  #drib_1,
  #drib_2,
  #drib_3,
  #drib_4 {
    opacity: 1;
    transform: translate(0, 0);
    transition-delay: 1.3s;
  }
  #drib_1 {
    transition-delay: 1.6s;
  }
  #drib_2 {
    transition-delay: 1.5s;
  }
  #drib_3 {
    transition-delay: 1.7s;
  }
  &.index_page {
    #content_block {
      visibility: visible;
      pointer-events: auto;
      transition-delay: 0s;
    }
    #under {
      /*display: block;*/
    }
    #full_screen {
      .wrapp,
      #nav_text_slider {
        opacity: 1;
        transform: translateY(0);
        transition-delay: 1.5s;
      }
    }
  }
  .slide_main {
    .text {
      opacity: 0;
    }
  }
}



