#nav_text_slider {
  padding: 0 0 0 58px;
  /*margin: 57px 0 0;*/
  position: relative;
  &:after {
    content: '';
    position: absolute;
    width: 41px;
    height: 2px;
    background-color: $y;
    top: 10px;
    left: 0;
  }
  a {
    color: #4e4e4e;
    display: inline-block;
    font-size: 16px;
    font-weight: 700;
    position: relative;
    margin: 0 17px 0 0;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      margin: -5px 0 0 -5px;
      width: 7px;
      height: 7px;
      border: 1px solid #a0a0a0;
      border-radius: 100%;
      opacity: 1;
      transform: scale(1);
      transition: transform .55s .2s cubic-bezier(.165,.84,.44,1),opacity .45s .2s cubic-bezier(.165,.84,.44,1);
    }
    span {
      opacity: 0;
      transition: transform .7s cubic-bezier(.23,1,.32,1),opacity .45s cubic-bezier(.165,.84,.44,1);
      display: inline-block;
      &.f {
        transform: translateY(-10px);
      }
      &.l {
        transform: translateY(10px);
      }
    }
    &.active {
      span {
        opacity: 1;
        transition: transform .7s .2s cubic-bezier(.23,1,.32,1),opacity .45s .2s cubic-bezier(.165,.84,.44,1);
        &.f,
        &.l {
          transform: translateY(0);
        }
      }
      &:before {
        width: 41px;
        opacity: 1;
      }
      &:after {
        opacity: 0;
        transform: scale(1.3);
        transition: transform .55s cubic-bezier(.165,.84,.44,1),opacity .45s cubic-bezier(.165,.84,.44,1);
      }
    }
  }
}

