  .address {
    font-size: 14px;
    font-weight: 300;
    line-height: 23px;
    margin: 36px 0 0;
    cursor: pointer;
    div {
      color: $y;
      font-size: 16px;
      font-weight: 700;
    }
    &:hover {
      div {
        text-decoration: underline;
      }
    }
  }
  form {
    margin: 137px 0 0;
    .title_form {
      font-size: 18px;
      font-weight: 700;
      margin: 0 0 30px;
    }
    .flex_field {
      display: flex;
      justify-content: space-between;
      .field {
        width: 48%;
      }
    }
    .button {
      display: flex;
      margin: 43px 0 0;
      align-items: center;
      position: relative;


      $animationLength: 1.5s;
      $animationRadius: 12px;
      $dotSize: 5px;

      .sampleContainer {
        position: absolute;
        left: 0;
        top: -4px;
        opacity: 0;
        transition: .1s;
        &.show {
          opacity: 1;
        }
      }

      .loader_form {
          position: relative;
          width: $animationRadius * 3 + $dotSize;
          height: $dotSize;
          margin: $animationRadius auto;
      }

      .dot {
          display: inline-block;
          width: $dotSize;
          height: $dotSize;
          border-radius: $dotSize * 0.5;
          background: #ccc;
          position: absolute;
      }

      .dot_1 {
          animation: animateDot1 $animationLength linear infinite;
          left: $animationRadius;
          background: $y;
      }

      .dot_2 {
          animation: animateDot2 $animationLength linear infinite;
          animation-delay: $animationLength / 3;
          left: $animationRadius * 2;
      }

      .dot_3 {
          animation: animateDot3 $animationLength linear infinite;
          left: $animationRadius;
      }

      .dot_4 {
          animation: animateDot4 $animationLength linear infinite;
          animation-delay: $animationLength / 3;
          left: $animationRadius * 2;
      }

      @keyframes animateDot1 {
          0%{ transform: rotate(0deg) translateX(-$animationRadius); }
          25% { transform: rotate(180deg) translateX(-$animationRadius); }
          75% { transform: rotate(180deg) translateX(-$animationRadius); }
          100% { transform: rotate(360deg) translateX(-$animationRadius); }
      }

      @keyframes animateDot2 {
          0%{ transform: rotate(-0deg) translateX(-$animationRadius); }
          25% { transform: rotate(-180deg) translateX(-$animationRadius); }
          75% { transform: rotate(-180deg) translateX(-$animationRadius); }
          100% { transform: rotate(-360deg) translateX(-$animationRadius); }
      }

      @keyframes animateDot3 {
          0%{ transform: rotate(0deg) translateX($animationRadius); }
          25% { transform: rotate(180deg) translateX($animationRadius); }
          75% { transform: rotate(180deg) translateX($animationRadius); }
          100% { transform: rotate(360deg) translateX($animationRadius); }
      }

      @keyframes animateDot4 {
          0%{ transform: rotate(-0deg) translateX($animationRadius); }
          25% { transform: rotate(-180deg) translateX($animationRadius); }
          75% { transform: rotate(-180deg) translateX($animationRadius); }
          100% { transform: rotate(-360deg) translateX($animationRadius); }
      }

    }
    .success {
      color: #a3a3a3;
      font-weight: 300;
      transition: .3s;
      &.hidden{
        opacity: 0;
        pointer-events: none;
      }
    }
    button {
      cursor: pointer;
      background: none;
      position: relative;
      font-size: 12px;
      text-transform: uppercase;
      font-weight: 900;
      letter-spacing: .2em;
      display: inline-block;
      padding-left: 46px;
      margin: auto 0 auto auto;
      transition: .3s;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        margin-top: -1px;
        height: 1px;
        width: 30px;
        background: $y;
        transition: .3s;
      }
      &:hover {
        color: $y;
        &:after {
          width: 46px;
          left: -16px;
        }
      }
    }
  }
}
