#next_service {
  .drib_svg {
    transform: translate(-30px, -60px);
    opacity: 0;
    transition: transform 1.5s .9s ease, opacity 1.3s .9s ease;
  }
  .animate {
    .drib_svg {
      transform: translate(0);
      opacity: 1;
    }
  }
}
.animate_block {
  opacity: 0;
  transition: all 0.8s linear;
  .item_animate {
    transform: translate3d(0, 80px, 0);
    opacity: 0;
    transition: transform 1.5s ease, opacity 1s ease;
    &:nth-child(2) {
      transition-delay: .2s;
    }
    &:nth-child(3) {
      transition-delay: .4s;
    }
    &:nth-child(4) {
      transition-delay: .6s;
    }
    &:nth-child(5) {
      transition-delay: .8s;
    }
    &:nth-child(6) {
      transition-delay: 1s;
    }
    &:nth-child(7) {
      transition-delay: 1.2s;
    }
    &:nth-child(8) {
      transition-delay: 1.4s;
    }
    &:nth-child(9) {
      transition-delay: 1.6s;
    }
    &:nth-child(10) {
      transition-delay: 1.8s;
    }
  }
  .barrel {
    img {
      transform-origin: 0 100%;
      transform: scaleY(0);
      opacity: 0;
      transition: .3s;
      &:nth-child(2) {
        transition-delay: .1s;
      }
      &:nth-child(3) {
        transition-delay: .2s;
      }
      &:nth-child(4) {
        transition-delay: .3s;
      }
      &:nth-child(5) {
        transition-delay: .4s;
      }
      &:nth-child(6) {
        transition-delay: .5s;
      }
      &:nth-child(7) {
        transition-delay: .6s;
      }
      &:nth-child(8) {
        transition-delay: .7s;
      }
      &:nth-child(9) {
        transition-delay: .8s;
      }
    }
  }
  .text_animate {
    transform: translateY(30px);
    opacity: 0;
    transition: transform 1.5s ease,opacity 1s ease;
  }
  .text_animate1 {
    transition-delay: .4s;
  }
  .text_animate2 {
    transition-delay: .6s;
  }
  .text_animate3 {
    transition-delay: .8s;
  }
  .text_animate4 {
    transition-delay: .9s;
  }

  &.item_partners {
    transform: translateY(70px);
    opacity: 0;
    transition: transform 1.5s ease,opacity 1s ease;
  }
  .item_partners1 {
    transition-delay: .2s;
  }
  .item_partners2 {
    transition-delay: .4s;
  }
  .item_partners3 {
    transition-delay: .6s;
  }
  .item_partners4 {
    transition-delay: .8s;
  }


  &.animate {
    opacity: 1;
    .item_animate {
      transform: translate3d(0, 0, 0);
      opacity: 1;
    }
    .barrel {
      img {
        transform: scaleY(1);
        opacity: 1;
      }
    }
    .text_animate {
      transform: translateY(0);
      opacity: 1;
    }
    .gradient_animation {
      i {
        transform: translate3d(0, 0, 0);
      }
    }
    &.item_partners {
      opacity: 1;
      transform: translateY(0);
    }
    &.statistics .item {
      transform: translateY(0);
      opacity: 1;
    }
  }
}







